
.navbar {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    background-color: #0b0c10;
    color: white;
  }
  
.brand-title {
    font-size: 1.5rem;
    margin: .5rem;
    display: flex;
    gap: 1em;
    text-align: center;
    margin-left: 1em;
}

.brand-title a {
    align-self: center;
    text-decoration: none;
    color: white;
    transition: color 0.3s ease 0s;
}

.brand-title a:hover {
    color: #ff6600;
}

.brand-title img {
    width: 100%;
    height: 2em;
    width: 2em;
    border-radius: 4em;
    object-fit: cover;

}

.toggle-button {
    position: absolute;
    top: 1.25rem;
    right: 1rem;
    display: none;
    width: 30px;
    height: 30px;
    color: white;
    justify-self: center;
    align-self: center;
  }

.navbar-links {
    height: 100%;
}

.navbar-links ul {
    display: flex;
    margin: 0;
    padding: 0;
}
  
.navbar-links li {
    list-style: none;
}

.navbar-links li a {
    display: block;
    text-decoration: none;
    color: white;
    padding: 1rem;
    transition: color 0.3s ease 0s;
}

.navbar-links li a:hover {
    color: #ff6600;
}

.hero-area {
    display: grid;
    grid-template-columns: 10fr 1fr;
    color: white;
    padding: 1em;
    padding-top: 20em;
    padding-bottom: 15em;
    background-color: #1b222c;
}

.hero-text {
    display: grid;
    grid-auto-flow: row;
    text-align: center;
    justify-content: center;
    align-self: center;
    justify-self: center;
}

.hero-text h1 {
    font-size: 2.16em;
}

.hero-text p {
    font-size: 1.16em;
    font-weight: 300;
}

.hero-text button {
    font-size: 2em;
    font-weight: 300;
    width: 70%;
    justify-self: center;
    align-self: center;
    border: none;
    border-radius: 7px;
    padding: 0.4em;
    background-color: #0b0c10;
    color: white;
    transition: color 0.3s ease 0s;
    margin-top: 1em;
}

.hero-text button:hover {
    color: #ff6600;
}

.socials {
    display: block;
    align-self: center;
    justify-self: end;
}

.socials h2 {
    margin: 0;
    margin-bottom: 0.2em;
    font-weight: 600;
    font-size: 1.6em;
    transition: color 0.3s ease 0s;
}

.socials h2:hover {
    color: #ff6600;
}

.social svg{
    margin-bottom: 0.5em;
    transition: fill 0.3s ease 0s;
}

.social svg:hover {
    fill: #ff6600;
}

.information {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1em;
    padding: 2em;
    padding-top: 5em;
    padding-bottom: 15em;
    color: white;
    background-color: #161c23;
}

.information h2 {
    font-size: 2em;
}


@media only screen and (max-width: 900px) {
    .socials {
        display: none;
    }

    .hero-area {
        display: block;
    }

    .information {
        display: grid;
        grid-template-columns: 1fr;
    }

}

.skills {
    display: flex;
    flex-wrap: wrap;
    height: min-content;
}

.skills p {
    margin: 0;
    padding: 0;
    text-align: center;
    color: white;
    background-color: #ff6600;
    margin: 0.5em;
    height: min-content;
    width: max-content;
    padding: 0.4em 1em 0.4em 1em;    
    text-align: center;
    border-radius: 10px;

}


@media only screen and (max-width: 700px) {
    .navbar {
        flex-direction: column;
        align-items: flex-start;
    }

    .toggle-button {
        display: block;
    }

    .navbar-links {
        display: none;
        width: 100%;
    }
  
    .navbar-links ul {
        width: 100%;
        flex-direction: column;
    }
  
    .navbar-links ul li {
        text-align: center;
    }
  
    .navbar-links ul li a {
        padding: .5rem 1rem;
    }
  
    .navbar-links.active {
        display: flex;
    }
}

.projects-section {
    display: grid;
    grid-auto-flow: row;
    padding-top: 5em;
    padding-bottom: 15em;
    background-color: #0e1317;
}

.projects-section h2 {
    font-size: 2em;
    color: white;
    margin: 0;
    margin-left: 1em;
}

.projects-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

.project-image {
    display: flex;
    justify-content: center;
}

.project-card img {
    width: 90%;
    height: 20em;
    object-fit: cover;
    margin-top: 1em;
    border-radius: 0.5em;

}

.project-card {
    display: flex;
    flex-direction: column;
    justify-items: center;
    justify-content: center;
    box-shadow: 5px 5px 20px #0b0c10;
    margin: 1em;
    background-color: white;
}

.project-info {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 0.5em;
}

.project-card button {
    color: white;
    background-color: #0b0c10;
    margin: 1em;
    border: none;
    border-radius: 10px;
    padding: 0.5em;
    width: 80%;
    align-self: center;
    font-size: 1em;
    transition: color 0.3s ease 0s;
    margin-top: auto;

}

.project-card button:hover {
    color: #ff6600;
}

@media only screen and (max-width: 1300px) {
    .projects-container {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media only screen and (max-width: 900px) {
    .projects-container {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}

@media only screen and (max-width: 600px) {
    .projects-container {
        display: grid;
        grid-template-columns: 1fr;
    }
}

footer {
    display: flex;
    flex-direction: column;
    gap: 0;
    background-color: black;
    color: white;
    text-align: center;
    padding: 7em 0 7em 0 ;
}

footer p, h2 {
    padding: 0;
    margin: 0.3em;
}

.contact-icons {
    display: flex;
    gap: 1em;
    align-self: center;
    margin-top: 2em;
}